/* Global styles for preventing unwanted scrolling */
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
}

#root {
  height: 100%;
  overflow: hidden;
}

/* Ensure all child elements inherit box-sizing */
*, *::before, *::after {
  box-sizing: inherit;
}